import React from "react";
import styled from "styled-components";
import { InnerLayout } from "../styles/Layouts";
import Title from "../Components/Title";
import SmallTitle from "../Components/SmallTitle";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import SchoolIcon from "@material-ui/icons/School";
import ResumeItem from "../Components/ResumeItem";

function Resume() {
  const briefcase = <BusinessCenterIcon />;
  const school = <SchoolIcon />;
  return (
    <ResumeStyled>
      <Title title={"Resume"} span={"resume"} />
      <InnerLayout>
        <div className="small-title">
          <SmallTitle icon={briefcase} title={"Working Experience"} />
        </div>
        <div className="resume-content">
          <ResumeItem
            year={"2020 - 2020"}
            title={"Frontend Developer Intern"}
            subTitle={"Vyorius LLP"}
            text={
              "Worked with ReactJS, Plotly, Python (Flask, Pandas, pyulog, Firebase and Cesium ION Earth. Made a Web Application that visualize data from a .ulog file."
            }
          />
        </div>
        <div className="small-title u-small-title-margin">
          <SmallTitle icon={school} title={"Educational Qualifications"} />
        </div>
        <div className="resume-content ">
          <ResumeItem
            year={"2021 - 2024"}
            title={"B.Tech Computer Science and Engineering"}
            subTitle={
              "Jaypee Institute of Information Technology, Noida Sector 62"
            }
            text={
              "Currently Undergraduate Sophomore at Jaypee Institute of Information Technology, Noida Sector 62. I'll Complete my graduation by May 2024."
            }
          />
          <ResumeItem
            year={"2018-2021"}
            title={"Diploma Computer Science and Engineering"}
            subTitle={"Uttaranchal University, Dehradun"}
            text={
              "Completed Diploma in Computer Science and Engineering from Uttaranchal University, Dehradun. I'm be pursuing my B.Tech since May 2021."
            }
          />
        </div>
      </InnerLayout>
    </ResumeStyled>
  );
}

const ResumeStyled = styled.section`
  .small-title {
    padding-bottom: 3rem;
  }
  .u-small-title-margin {
    margin-top: 4rem;
  }

  .resume-content {
    border-left: 2px solid var(--border-color);
  }
`;
export default Resume;
